@import "../../../variables";

.sidemenu-item {
    width: 250px;
    font-weight: 700;
    font-size: 18px;
    text-decoration: none;
    color: black;
    background-color: white;
    padding: 5px 20px;
    border: 5px solid black;
    border-left: 0;
    border-radius: 0 30px 30px 0;
    margin-bottom: 5px;
}
.sidemenu-item-active {
    color: white;
    background-color: black;
    border-color: white;
}
